import { generatePath, useNavigate } from "react-router-dom"
import { BRIEF_ROUTE_DETAIL_VIEW_LATEST } from "@rp/react/web/router"

type UseNavToBriefConfigDetailViewState = {
  navigateToBriefConfigDetailView: (briefId: string) => void
}

const useNavigateToBriefConfigDetailView = (): UseNavToBriefConfigDetailViewState => {
  const navigate = useNavigate()

  const navigateToBriefConfigDetailView = (briefId: string) =>
    navigate(generatePath(BRIEF_ROUTE_DETAIL_VIEW_LATEST, { briefId }))

  return { navigateToBriefConfigDetailView }
}

export { useNavigateToBriefConfigDetailView }
export type { UseNavToBriefConfigDetailViewState }
